<template>
  <w-app title='Package Manager' class='app--package-manager animated fadeInDown' 
  @keyup.up="monitorKeys('up')"
  @keyup.down="monitorKeys('down')"
  :has-sub-nav='false' >
    <label class="label">Application file:</label>
    <p class='control'>
      <input @change='loadApp' class="the-file-input" type="file">
    </p>
    <p class='control'>
      <a @click='saveApp' class="button is-info">
        <span class="icon">
          <i class="fa fa-cloud-upload"></i>
        </span>
        <span>Install</span>
      </a>
    </p>
  </w-app>
</template>

<script>
import wApp from '../wApp'
let localforage = require('localforage')
// let fetch = require('whatwg-fetch')
// import 'whatwg-fetch'
export default {
  data () {
    return {
      isSaving: false,
      appData: '',
      appTitle: ''
    }
  },
  components: {
    wApp
  },
  methods: {
    saveApp (e) {
      this.$set('isSaving', true)
      localforage.setItem('.app--' + this.$get('appTitle'), this.$get('appData'))
      .then(() => {
        this.$set('isSaving', false)
        this.$dispatch('installApp', this.$get('appTitle'))
      })
    },
    loadApp (file) {
      console.log('file:', file.target.files[0])
      this.$set('appTitle', file.target.files[0].name.slice(0, -3))
      var reader = new FileReader()
      // inject an image with the src url
      reader.onload = (event) => {
        console.log('file target:', event.target)
        let url = event.target.result
        this.$set('appData', url)
      }
      // when the file is read it triggers the onload event above.
      reader.readAsText(file.target.files[0])
    }
  }
}
</script>

<style lang='sass' >
.card.app--package-manager
  figure
    margin-bottom: 1em
    box-shadow: 0px 0px 5px black
</style>



